.header {
  position: relative;
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(180deg, #242b33 11.98%, rgba(45, 52, 60, 0.38) 51.04%, #242b33 92.19%), url(../../img/header.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.header__nav {
  margin: 20px 0;
}

.header__nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__main {
  height: 100%;
}

.header__main-container {
  display: flex;
  flex-direction: column;
  justify-content: end;
  height: 100%;
}

.header__content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  max-width: 582px;
  height: 70vh;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
}

.header__content h1 {
  margin-bottom: 30px;
  font-family: 'PlayfairDisplay', sans-serif;
  font-size: 48px;
  font-weight: 700;
  color: var(--color-accent);
}

.header__mouse {
  position: absolute;
  top: 250px;
  left: 50%;
  transform: translateX(-50%);
}

.header__mouse-icon {
  width: 40px;
  height: 40px;
  animation: down-up 1s cubic-bezier(0.1, 0.25, 0.1, 1) 0s infinite alternate both;
}

.logo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo__img {
  max-width: 70px;
}

.logo__name {
  color: var(--color-accent);
}

.nav__list {
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 40px;
}

.contacts {
  display: flex;
  justify-content: space-around;
  margin-top: 20%;
}

.contacts__icon {
  max-width: 21px;
  max-height: 21px;
  fill: var(--color-white);
}

.contacts__link, .contacts__icon {
  transition: all .5s ease-out;
}

.contacts__link:hover {
  color: var(--color-accent);
}

.contacts__link:hover svg {
  fill: var(--color-accent);
}

@keyframes down-up {
  from {
    transform: translateY(-5px);
  }
  to {
    transform: translateY(3px);
  }
}
